 z-index: 9999;\n  max-width: 300px;\n  -webkit-box-shadow: 0 0 5px #aaa;\n  box-shadow: 0 0 5px #aaa;\n  font-size: 12px;\n  color: #666;\n  background: rgba(240, 240, 240, 0.8);\n  border-width: 2px;\n}\n.ui-tooltip .ui-tooltip-content {\n  word-break: break-all;\n}\n.ui-helper-hidden,\n.ui-helper-hidden-accessible {\n  display: none;\n}\nhtml.gray {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(grayscale=1);\n  -webkit-filter: grayscale(100%);\n  -moz-filter: grayscale(100%);\n  filter: grayscale(100%);\n}\n@media only screen and (-webkit-min-device-pixel-ratio: 2) {\n  html.gray {\n    -webkit-transform: translateZ(0);\n  }\n}\n.text-ellipsis {\n  -o-text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n.terminalAdmin,\n.taskprogress {\n  cursor: pointer;\n}\n.taskprogress {\n  cursor: pointer;\n}\npre {\n  display: inline;\n  margin: 0;\n  -o-text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  font-family: '微软雅黑', 'Helvetica Neue', Arial, sans-serif;\n}\n/*去除浏览器默认的clear-icon*/\ninput[type=password]::-ms-clear {\n  display: none;\n  width: 0;\n  height: 0;\n}\ninput[type=password]::-ms-reveal {\n  display: none;\n  width: 0;\n  height: 0;\n}\ninput::-webkit-search-decoration,\ninput::-webkit-search-cancel-button,\ninput::-webkit-search-results-button,\ninput::-webkit-search-results-decoration {\n  display: none;\n}\ninput[type=\"password\"]::-webkit-soft-keyboard-button {\n  display: none !important;\n}\ninput[type=checkbox],\ninput[type=radio] {\n  -webkit-appearance: none;\n  appearance: none;\n  font: normal normal normal 14px/1 FontAwesome;\n  text-rendering: auto;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  font-size: 14px;\n  border: 0;\n  outline: 0;\n  vertical-align: middle;\n}\ninput[type=\"checkbox\"]:before {\n  width: 14px;\n  display: inline-block;\n  content: \"\\f096\";\n}\ninput[type=\"checkbox\"]:checked:before {\n  width: 14px;\n  content: \"\\f046\";\n}\ninput[type=\"checkbox\"]:indeterminate:before {\n  width: 14px;\n  content: \"\\f146\";\n}\ninput[type=\"checkbox\"]:checked,\ninput[type=\"checkbox\"]:indeterminate {\n  color: #8cc24e;\n}\ninput[type=\"checkbox\"]:disabled {\n  color: #CCC;\n}\ninput[type=\"text\"]:disabled {\n  background: #f1f1f1;\n}\ninput[type=radio]:before {\n  content: \"\\f10c\";\n}\ninput[type=radio]:checked:before {\n  content: \"\\f192\";\n}\ninput[type=radio]:checked {\n  color: #8cc24e;\n}\ninput[type=radio]:disabled {\n  color: #CCC;\n}\ni.switch {\n  cursor: pointer;\n}\ni.switch[data-status=\"on\"] {\n  font-size: 20px;\n  color: #8cc24e;\n}\ni.switch[data-status=\"off\"] {\n  font-size: 20px;\n  color: #666;\n}\ni.switch.disabled {\n  color: #CCC;\n}\n.fa {\n  -webkit-text-stroke-width: 0.2px;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  margin-left: 5px;\n}\n.nowrap {\n  white-space: nowrap;\n}\n.none {\n  display: none;\n}\n.link {\n  color: #5691d3;\n}\n.delete-box,\n.move-box {\n  height: auto;\n  margin: 28px;\n}\n.upgrade_box {\n  position: absolute;\n  background: rgba(0, 0, 0, 0.5);\n  z-index: 1050;\n  left: 0;\n  top: 0;\n}\n.upgrade_box .process_container {\n  position: absolute;\n  transform: translate(-50%, -50%);\n  top: 50%;\n  left: 50%;\n  background: #FFF;\n  margin: 1em;\n  border: 1px solid #ccc;\n  font-size: 12px;\n  color: #666;\n}\n.upgrade_box .process_container .popup-body {\n  position: relative;\n  margin: 16px 30px;\n  line-height: 22px;\n}\n.upgrade_box .process_container .check_upgrade_loading {\n  text-align: center;\n}\n.upgrade_box .process_container label {\n  line-height: 22px;\n  vertical-align: top;\n}\n.upgrade_box .process_container .loading_process {\n  position: relative;\n  display: inline-block;\n  width: 250px;\n  height: 16px;\n  background: #B7BAB4;\n  line-height: 16px;\n  min-height: 0;\n  margin: 0;\n  top: 0;\n  -webkit-border-radius: 10px;\n  -moz-border-radius: 10px;\n  border-radius: 10px;\n  overflow: hidden;\n}\n.upgrade_box .process_container .loading_process > i {\n  position: relative;\n  z-index: 1000;\n  left: 45%;\n}\n.upgrade_box .process_container .loading_process .proc_width {\n  position: absolute;\n  width: 250px;\n  height: 16px;\n  left: -250px;\n  background: #82bd3e;\n  -webkit-border-radius: 10px;\n  -moz-border-radius: 10px;\n  border-radius: 10px;\n}\n.upgrade_box .process_container .upgrade-container {\n  margin: 1em;\n  max-height: 500px;\n  max-width: 600px;\n  overflow-y: auto;\n}\n.upgrade_box .process_container .upgrade-container .no_content {\n  color: #666;\n  font-size: 12px;\n}\n.upgrade_box .process_container .upgrade-container > h2 {\n  font-size: 1.2em;\n  margin: 0.8em 0;\n  color: #333;\n}\n.upgrade_box .process_container .upgrade-container > ul {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  word-wrap: word-break;\n  overflow: hidden;\n}\n.upgrade_box .process_container .upgrade-container > ul > li {\n  position: relative;\n  display: block;\n  padding-left: 12px;\n  font-size: 12px;\n  color: #666;\n  line-height: 26px;\n  vertical-align: top;\n}\n.upgrade_box .process_container .upgrade-container > ul > li:before {\n  position: absolute;\n  left: 0;\n  line-height: 26px;\n  vertical-align: top;\n  font-family: FontAwesome;\n  display: inline-block;\n  content: '\\f041';\n  color: #82bd3e;\n}\n.pager-placeholder {\n  height: 50px;\n}\n.common-tools {\n  min-width: 1022px;\n  max-width: 1280px;\n  margin: 10px auto 0 auto;\n  height: 38px;\n  border: 1px solid #d8dadb;\n  background: white;\n  -webkit-border-radius: 3px;\n  -moz-border-radius: 3px;\n  border-radius: 3px;\n}\n.common-tools > .tools-back {\n  display: inline-block;\n  *display: inline;\n  *zoom: 1;\n  text-align: center;\n  width: 38px;\n  height: 38px;\n  font-size: 16px;\n  line-height: 38px;\n  color: #999;\n  background: #f7f8f9;\n}\n.search-wrap {\n  background: #f7f8f9;\n  position: re